.TodoSearch {
    background-color: transparent;
    height: 6.8rem;
    width: calc(100% - 10rem);
    margin: 0 5rem 3rem;
    text-align: center;
    font-size: 2.4rem;
    color: blanchedalmond;
    border-radius: 1.5rem;
    border: none;
    box-shadow: -5px -5px 9px rgba(255, 255, 255, 0.2),
    5px 5px 9px rgba(94, 104, 121, 0.2);
}

.TodoSearch::placeholder {
    color: rgba(255, 255, 255, 0.35);
    font-style: italic;
}

.TodoSearch:focus {
    outline: none;
  }